<h1 id="handling-concurrent-http-requests" tabindex="-1"><a class="header-anchor" href="#handling-concurrent-http-requests" aria-hidden="true">#</a> Handling Concurrent HTTP Requests</h1>
<p>So, our web application receives 1000 requests/second. How does it handle that as efficient as possible? Turns out there are some different ways to handle it, and Node.js started to use a new approach that turned out to be more scalable than the traditional approaches used. At least to some extent, but let's take a closer look at it!</p>
